'use client';

import { useEffect, useRef } from 'react';
import { Box } from '@mui/material';
import locations from '../data/locations';

export default function MapView({ selectedTrail, onSelectLocation }) {
  const mapRef = useRef(null);
  const mapInstanceRef = useRef(null);
  const markersRef = useRef([]);
  const infoWindowRef = useRef(null);

  useEffect(() => {
    if (!window.google || !window.google.maps) return;

    // Create map once
    if (!mapInstanceRef.current) {
      mapInstanceRef.current = new window.google.maps.Map(mapRef.current, {
        center: { lat: 53.405292, lng: -6.378240 },
        zoom: 16,
        mapTypeId: 'satellite',
        mapTypeControl: false,
        fullscreenControl: false,
      });

      infoWindowRef.current = new window.google.maps.InfoWindow();
    }

    const map = mapInstanceRef.current;

    // Clear old markers
    markersRef.current.forEach((marker) => marker.setMap(null));
    markersRef.current = [];

    // Decide which locations to show
    const visibleLocations = selectedTrail
      ? locations
          .filter((l) => l.trail === selectedTrail)
          .sort((a, b) => a.order - b.order)
      : locations.filter((l) => !l.trail);

    // Add markers
    visibleLocations.forEach((location, index) => {
      const marker = new window.google.maps.Marker({
        position: { lat: location.lat, lng: location.lng },
        map,
        label: location.order
          ? {
              text: `S${location.order}`,
              color: 'white',
              fontWeight: 'bold',
            }
          : undefined,
      });

      marker.addListener('click', () => {
        const content = `
          <div style="max-width:220px">
            <strong>${location.name}</strong><br/>
            ${location.description ?? ''}<br/>
            <em>${location.accessibility ?? ''}</em>
          </div>
        `;

        infoWindowRef.current.setContent(content);
        infoWindowRef.current.open(map, marker);

        onSelectLocation?.(location);
      });

      markersRef.current.push(marker);
    });
  }, [selectedTrail, onSelectLocation]);

  return (
    <Box
      ref={mapRef}
      sx={{
        width: '100%',
        height: '100%',
      }}
    />
  );
}
